$(".faq-card__title").click(function () {
    if(window.innerWidth <= 992){
        $(this).parents(".faq-card").toggleClass("_active")
        $(this).siblings(".faq-card__tooltip").slideToggle()
    }
})

$("._show-all-faq").click(function () {
    $(this).parents(".faq").toggleClass("_all")
})
